import Vue from "vue";
import Vuex from "vuex";
import createPersistedState from "vuex-persistedstate";

Vue.use(Vuex);

export const store = new Vuex.Store({
  state: {
    productos: [
      {
        id: "travertino",
        nombre: "Travertino",
        imagen: "public/images/travertino.jpg",
        imagen_banner: "public/images/travertino-banner.jpg",

        desc: [
          "Con un marcado énfasis en el genuino valor de las rocas sedimentarias, formadas durante millones de años en el fondo del Mar, nos enorgullese poder ofrecer piedras de travertino. Sus nobles cualidades de resistencia, versatilidad, como así también sus propiedades aislante térmico y acústico, han sido utilizadas para la construcción de grandes monumentos, palacios y fortificaciones por diversas civilizaciones a lo largo del tiempo, en lugares como el Mediterráneo.",
          "A través de nuestro trabajo con la piedra, queremos mantener viva esta cultura ancestral en el ámbito de la arquitectura y el diseño de interiores. Los distintos lenguajes arquitectónicos dan lugar a creaciones que propcian la armonía entre exterior e interior. Apoyados en la hermosura y nobleza de nuestra piedra, armonizamos espacios donde puede disfrutarse de la luz, el sol, la naturaleza y las vistas. Nuestros productos están orientados a lo largo de este estilo que puede ser rústico, clásico o contemporáneo, pero siempre tiende a expresar un estilo de vida relajado al aire libre."
        ]
      },
      {
        id: "piedra-onix",
        nombre: "Piedra de Onix",
        imagen: "public/images/piedra-onix.jpg",
        imagen_banner: "public/images/piedra-onix-banner.jpg",
        desc: [
            "El ónix blanco, piedra de origen volcánico, tiene su origen por la acumulación de gases volcánicos. Es notable como la violencia de un volcán puede crear algo tan bello como esta piedra. ",
            "Es precisamente ese origen que le imprime su característica de translucencia tan peculiar y a la vez hermoso. Considerada también una piedra semipreciosa, es utilizada en la decoración de las más lujosas viviendas, embelleciendo baños, cocinas y salas de estar.",
            "Asimismo, y debido a su dureza, es utilizada para las más fascinantes artesanías, esculturas, juegos de ajedrez, etc."
        ]
      },
      {
        id: "piedra-paisaje",
        nombre: "Piedra del paisaje",
        imagen: "public/images/piedra-paisaje.jpg",
        imagen_banner: "public/images/piedra-paisaje-banner.jpg",

        desc: [
            "Hay muchos usos excelentes de la piedra natural, pero uno de los más bellos es la piedra en el paisaje. ",
            "¿Qué puede hacer que un pedazo de piedra natural sea aceptable como piedra de paisaje? ",
            "En general, una piedra natural que se utilizará en un entorno paisajístico debe ser capaz de tolerar ciclos de congelación-descongelación y fluctuaciones extremas de temperatura en general. El potencial para la presencia constante de agua también debe ser tolerable. Por último, el material debe tener un acabado relativamente antideslizante.",
            "Nuestra gama de productos posee estas cualidades, sumada a su belleza natural, lo que las hace ideal para ayudar y embellecer proyectos paisajísticos y de jardinería."
        ]
      },
      {
        id: "granza",
        nombre: "Granza",
        imagen: "public/images/granza.jpg",
        imagen_banner: "public/images/granza-banner.jpg",
        desc: [
            "Entre muchos otros, contamos con granza de diferentes tipos de piedra (travertino, ónix blanco, ónix veteado y ónix verdoso)."
        ]
      },
      {
        id: "onix-blanco",
        nombre: "Ónix",
        imagen: "public/images/onix-blanco.jpg",
        imagen_banner: "public/images/onix-blanco-banner.jpg",

        desc: [
            "El ónix en su estado natural es una piedra inigualable para desarrollar todo tipo de proyectos de paisajismo. Su belleza natural, sumado a sus cualidades y translucencia, la hacen ideal para complementar con luces, cursos de agua, etc."
        ]
      },
      {
        id: "canto-rodado",
        nombre: "Canto rodado",
        imagen: "public/images/canto-rodado.jpg",
        imagen_banner: "public/images/canto-rodado-banner.jpg",

        desc: [
            "La nobleza, rusticidad y solidez de la piedra de río, la hacen ideal para cualquier trabajo de paisajismo y jardinería. La variedad de tamaños, formas y colores le dan una gran versatilidad haciéndola optima para adaptarse a cualquier proyecto."
        ]
      }
    ]
  },
  plugins: [createPersistedState()],

  getters: {
    productos: state => {
      return state.productos;
    }
  },
  actions: {}
});



